import errors from 'feathers-errors/lib/index';
import HttpStatus from './httpStatus';
/**
 * Creates a new HTTP error
 *
 * @param name of this error
 * @param message to be displayed
 * @param code HTTP error code
 * @param errors Array of erros
 * @returns {errorTypes.FeathersError}
 */
export let create = (name, message, code, errors) => {
	let error = new new errors.GeneralError(message);
	error.name = name;
	error.code = code;
	error.errors = errors;
	return error;
};
/**
 * The server does not support the functionality required to fulfill the request. This is the appropriate response
 * when the server does not recognize the request method and is not capable of supporting it for any resource.
 *
 * @param message
 * @param errors
 * @constructor
 */
export let MethodNotImplemented = (message = 'Method Not Implemented', errors) => create('MethodNotImplemented', message, HttpStatus.NOT_IMPLEMENTED, errors);
/**
 * The server does not support the functionality required to fulfill the request. This is the appropriate response
 * when the server does not sent a text response.
 *
 * @param message
 * @param errors
 * @constructor
 */
export let NoContent = (message = 'No Content', errors) => create('NoContent', message, HttpStatus.NO_CONTENT, errors);
